<template>
  <div class="chat">
    <section ref="chatArea" class="chat-area">
      <chatbox
        :key="item.uid"
        v-for="item in Mseeages"
        :message="item.text"
        :author="item.user.email"
        :user_id="me"
        :time="changeTime(item.createdAt)"
      />
    </section>
    <textbox @sendMsg="sendMsg" />
  </div>
</template>

<script>
import textbox from "@/components/textbox.vue";
import chatbox from "@/components/chatbox";
import moment from "moment";

// eslint-disable-next-line no-unused-vars
import { async } from "q";

export default {
  name: "home",
  data() {
    return {};
  },
  computed: {
    Mseeages: {
      get() {
        return this.$store.state.msg.messages;
      }
    },
    me: {
      get() {
        return this.$store.state.user.email;
      }
    }
  },
  methods: {
    changeTime(time) {
      return moment(time).format("HH:mm");
    },
    scrollToButtom() {
      const totalHeight = document.querySelector(".chat-area").scrollHeight;
      document.querySelector(".chat-area").scrollTop = totalHeight;
    },
    sendMsg(data) {
      this.$store.dispatch("msg/send", data);
    }
  },
  components: {
    textbox,
    chatbox
    // member
  },
  mounted() {
    this.scrollToButtom();
    this.$store.dispatch("msg/getAllMsg");

    this.sockets.subscribe("system", async data => {
      if (data.action === "login") {
        this.$bvToast.toast(`使用者 ${data.Msg} 登入`, {
          title: "登入通知",
          autoHideDelay: 5000,
          variant: "success",
          appendToast: true
        });
      }
    });

    this.sockets.subscribe("sendMsg", async data => {
      await this.$store.commit("msg/new", { data });
      this.scrollToButtom();
    });
  },
  create() {
    this.scrollToButtom();
  }
};
</script>

<style lang="scss">
@import "src/scss/home.scss";
</style>
