import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home";
import header from "@/components/header";
import login from "@/views/login";
import signup from "@/views/signup";
import member from "@/views/admin/member";
import createAdmin from "@/views/admin/createUser";

import store from "@/store";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    components: {
      header: header,
      mainbody: Home
    },
    meta: {
      title: "chatroom"
    }
  },
  {
    path: "/login",
    name: "login",
    components: {
      header: header,
      mainbody: login
    },
    meta: {
      title: "login"
    }
  },
  {
    path: "/signup",
    name: "signup",
    components: {
      header: header,
      mainbody: signup
    },
    meta: {
      title: "signup"
    }
  },
  {
    path: "/member/management",
    name: "management",
    components: {
      header: header,
      mainbody: member
    },
    meta: {
      title: "會員管理"
    }
  },
  {
    path: "/member/create",
    name: "createAdmin",
    components: {
      header: header,
      mainbody: createAdmin
    },
    meta: {
      title: "新建管理者"
    }
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  document.title = to.meta ? to.meta.title : document.title;
  store.dispatch("user/getProfile");
  if (to.name === "Home") {
    if (localStorage.Authorization) {
      next();
    } else {
      next({ name: "login" });
    }
  } else {
    next();
  }
});

export default router;
