<template>
  <div class="member">
    <i class="fas fa-user-plus member--icon" @click="createAdmin()"></i>
    <b-list-group>
      <b-list-group-item variant="dark">會員列表</b-list-group-item>
      <b-list-group-item
        :key="item._id"
        v-for="item in memberList"
        class="flex-column align-items-start"
      >
        <div class="">
          <h5 class="mb-1">
            {{ item.email }}
            <b-badge variant="warning" v-if="item.admin">管理者</b-badge>
            <b-badge variant="light" href="#" v-else @click="edit(item._id)">
              升為管理者
            </b-badge>
          </h5>
          <i
            class="far fa-trash-alt member--icon redicon"
            @click="remove(item._id)"
          ></i>
        </div>
      </b-list-group-item>
    </b-list-group>
  </div>
</template>

<script>
export default {
  name: "member",
  data() {
    return {};
  },
  methods: {
    edit(id) {
      this.$store.dispatch("user/upgradeUser", id);
    },
    createAdmin() {
      this.$router.push({ name: "createAdmin" });
    },
    remove(id) {
      this.$store.dispatch("user/removeUser", id);
    }
  },
  computed: {
    memberList: {
      get() {
        return this.$store.state.user.memberList;
      }
    }
  },
  mounted() {
    this.$store.dispatch("user/getAllUsers");

    this.sockets.subscribe("system", async data => {
      if (data.action === "removeUser" || data.action === "upgradeUser") {
        this.$store.dispatch("user/getAllUsers");
      }
    });
  }
};
</script>

<style lang="scss">
@import "src/scss/member.scss";
</style>
