import axios from "axios";
// import CryptoJS from "crypto-js";
// import base64 from "crypto-js/enc-base64";
import router from "@/router";

const http = {
  get: (path, data) => {
    var tmp = [];
    data = data || {};

    for (let i = 0; i < data.length; i++) {
      var val =
        typeof data[i] == "string" || typeof data[i] == "number"
          ? data[i]
          : data[i]
          ? data[i].join(",")
          : "";
      tmp.push(i + "=" + val);
    }

    return axios.get(
      process.env.VUE_APP_PATH +
        "/" +
        path +
        (tmp.length > 0 ? "?" + tmp.join("&") : "")
    );
  },
  post: (path, data) => {
    return axios.post(process.env.VUE_APP_PATH + "/" + path, data);
  },
  delete: (path, data) => {
    return axios.delete(process.env.VUE_APP_PATH + "/" + path, data);
  },
  patch: (path, data) => {
    return axios.patch(process.env.VUE_APP_PATH + "/" + path, data);
  }
};

