export default {
  namespaced: true,
  state: {
    user_id: "",
    email: "",
    loginState: false,
    admin: false,
    memberList: []
  },
  getters: {},
  actions: {
    login({ commit }, data) {
      let result, loginData;
      // encryptData = encrypt(JSON.stringify(data));
      result = http.post("user/login", data);

      result.then(res => {
        if (res.data.status === "success") {
          localStorage.Authorization = res.data.token;
          axios.defaults.headers.common["Authorization"] =
            "Bearer " + (localStorage.Authorization || "");
          loginData = "true";
          router.push({ name: "Home" });
        } else {
          loginData = "false";
        }
        commit("checkUser", res.data.data.user);
      });
      return loginData;
    },
    signup({ commit }, data) {
      let result, loginData;
      // encryptData = encrypt(JSON.stringify(data));
      result = http.post("user/signup", data);

      result.then(res => {
        if (res.data.status === "success") {
          localStorage.Authorization = res.data.token;
          axios.defaults.headers.common["Authorization"] =
            "Bearer " + (localStorage.Authorization || "");
          loginData = "true";
          router.push({ name: "Home" });
        } else {
          loginData = "false";
        }
        commit("checkUser", res.data.data.user);
      });

      return loginData;
    },
    // eslint-disable-next-line no-unused-vars
    adminSignup({ commit }, data) {
      let result;
      // encryptData = encrypt(JSON.stringify(data));
      result = http.post("admin/create", data).then(() => {
        router.push({ name: "management" });
      });

      return result;
    },
    getProfile({ commit }) {
      const result = http.get("user/me");
      result.then(res => {
        commit("checkUser", res.data.data.user);
      });
      return result;
    },
    getAllUsers({ commit }) {
      const result = http.get("user/all");
      result.then(res => {
        commit("allUsers", res.data.data.users);
      });
    },
    SignOut({ commit }) {
      return new Promise(resolve => {
        commit("reDefault");
        router.push({ name: "login" });
        resolve();
      });
    },
    // eslint-disable-next-line no-unused-vars
    removeUser({ commit }, id) {
      http.delete("admin/removeUser", {
        data: { id }
      });
    },
    // eslint-disable-next-line no-unused-vars
    upgradeUser({ commit }, id) {
      http.patch("admin/upgrade", { id });
    }
  },
  mutations: {
    checkUser(state, data) {
      state.loginState = true;
      state.email = data.email;
      state.user_id = data._id;
      state.admin = data.admin;
    },
    reDefault(state) {
      state.loginState = false;
      state.email = "";
      state.user_id = "";
      state.admin = false;
      localStorage.Authorization = "";
      axios.defaults.headers.common["Authorization"] = "Bearer ";
    },
    allUsers(state, data) {
      state.memberList = data;
    }
  }
};
