<template>
  <div>
    <b-container>
      <b-row class="my-1">
        <b-col sm="3">
          <label for="`type-email`">email :</label>
        </b-col>
        <b-col sm="9">
          <b-form-input
            id="`type-email`"
            type="email"
            v-model="email"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row class="my-1">
        <b-col sm="3">
          <label for="`type-password`">password :</label>
        </b-col>
        <b-col sm="9">
          <b-form-input
            id="`type-password`"
            type="password"
            v-model="password"
          ></b-form-input>
        </b-col>
      </b-row>
      <b-button variant="warning" @click="signup()">註冊</b-button>
    </b-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    signup() {
      // eslint-disable-next-line no-console
      console.log(this);
      this.$store.dispatch("user/adminSignup", {
        email: this.email,
        password: this.password
      });
    }
  }
};
</script>
